import React from 'react'
import Nav from '../components/common/nav'
import Footer from '../components/common/footer'
import Head from 'next/head'
import HowiItWorks from '../components/common/howitworks'
import {Collapse} from 'antd'
import config from '../config'
const {Panel} = Collapse;
export default () => {
    return (
        <div className="bg-gray-100" style={{backgroundImage: "url('/static/img/bg.svg')"}}>
            <Head>
                <title>CryptoFair - The easiest and fastest way to make money in crypto industry</title>
            </Head>
            <div className="bg-white">
                <div className="container mx-auto px-4">
                    <Nav />
                </div>
            </div>
            <div className="container mx-auto px-4 py-32">
                <div className="mb-8">
                    <p className="text-4xl text-cryp-text font-bold text-center mb-8">How It Works</p>
                    <div className="text-lg font-bold text-center mb-16">Four simple steps</div>
                    <HowiItWorks />
                </div>
                <div className="">
                    <p className="text-4xl text-cryp-text font-bold text-center mb-8">Detailed instructions</p>
                    <div className="">
                        <div className="flex flex-wrap mb-8">
                            <div className="w-full md:w-32 px-4">
                                <img src="/static/0.svg" alt="" className="w-32 md:w-full"/>
                            </div>
                            <div className="py-4 px-4">
                                <div className="font-bold text-xl">0. Ethereum</div>
                                <div className="font-bold text-lg">0.0.</div>
                                <div className="text-lg">If you haven't an Ethereum wallet, read more about it in <a href="/faqeth" className="">Ethereum FAQ</a></div>
                            </div>
                        </div>
                        <div className="flex flex-wrap mb-8">
                            <div className="w-full md:w-32 px-4">
                                <img src="/static/signin.png" alt="" className="w-32 md:w-full"/>
                            </div>
                            <div className="w-full md:flex-1 md:w-auto py-4 px-4">
                                <div className="font-bold text-xl">1. Sign up</div>
                                <div className="font-bold text-lg">1.1.</div>
                                <div className="">
                                    <div className="text-center"><img src="/static/Signup1.png" alt="" className="mx-auto"/></div>
                                </div>
                                <div className="text-lg">
                                    Send 0.08 ETH from your Ethereum wallet to your upline. And the first user came after you will return this amount back to you.
                                </div>
                                <div className="text-lg">For that send 0.08 ETH to the <a href="" className="">smart contract address</a> and enter a number of your upline Ethereum-wallet to the "Input DATA" MANDATORY field (otherwise, the smart contract will not accept the transfer and the funds will remain with you). If you use MetaMask, the transaction is done in 1 click, and the address of your upline will be inserted into the "Input DATA " field AUTOMATICALLY.</div>
                                <div className="text-lg">The smart contract instantly redirects the funds to your first-line upline (your direct inviter), and assigns you the first level in the system. A successful transaction will register you in the System and assign the 1st level to Your account (more details about the levels below).</div>
                                <div className="text-lg">Simple and clear guide on the page <a href={config.signup} className="">Sign up</a></div>
                                <div className="font-bold text-lg">1.2.</div>
                                <div className="">
                                    <div className="text-center">
                                        <img src="/static/Signup2.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="text-lg">These 0.05 ETH will return to you fully after the first referral.</div>
                            </div>
                        </div>
                        <div className="flex flex-wrap text-lg mb-8">
                            <div className="w-full md:w-32 px-4">
                                <img src="/static/Login.png" alt="" className="w-32 md:w-full"/>
                            </div>
                            <div className="w-full md:flex-1 md:w-auto py-4 px-4">
                                <div className="font-bold text-xl">2. Log In</div>
                                <div className="font-bold text-lg">2.1.</div>
                                <div className="">
                                    <div className="">
                                        <img src="" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="text-lg">
                                    <a href={config.login} className="">SIGN IN</a> the account, entering only the number of your Ethereum-wallet (without password), or via MetaMask.
                                </div>
                                <div className="">Account can not be hacked, because there is nothing to hack and there is no password. When you enter the address in your account, the information of this wallet will be taken from the blockchain Ethereum and displayed,</div>
                                <div className="">Account can not be blocked by anyone, including the administration of the project, and the wallet can not be changed to another.</div>
                                <div className="">Your Ethereum wallet also cannot be blocked due to decentralization. This is a feature of cryptocurrency.</div>
                                <div className="text-red-400">We never ask you to enter your private keys.</div>
                            </div>
                        </div>
                        <div className="flex flex-wrap mb-8 text-lg">
                            <div className="w-full md:w-32 px-4">
                                <img src="/static/Attract_referrals.png" alt="" className="w-32 md:w-full"/>
                            </div>
                            <div className="w-full md:flex-1 md:w-auto py-4 px-4">
                                <div className="font-bold text-xl">3. Attract or wait for referrals</div>
                                <div className="font-bold text-lg">3.1.</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Attract1.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">After purchasing Level 1 You can invite 3 referals personally. These are your 1st line referrals.</div>
                            <div className="font-bold text-lg">3.2.</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Attract2.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">If you have invited 3 referrals and then invited the 4th, he will be automatically directed to your referrals in order from the first in the list and if each of them has all 3 slots filled, he will be directed to the referral of your referral. If you sign up without a site directly through a smart contract overflows will work up to the 5th line and if you sign up through the site, overflows will work up to the 8th line . Details in FAQ.</div>
                                <div className="font-bold text-lg">3.3.</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/AttractFreereferrals.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">Free referrals are distributed evenly throughout the System, and starting with those uplines, who earlier registered in the System.</div>
                            </div>
                        </div>
                        <div className="flex flex-wrap mb-8">
                            <div className="w-full md:w-32 px-4">
                                <img src="/static/Income.png" alt="" className="w-32 md:w-full"/>
                            </div>
                            <div className="w-full md:flex-1 md:w-auto py-4 px-4 text-lg">
                                <div className="font-bold text-xl">4. Income</div>
                                <div className="font-bold text-lg">4.1. (1level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income1.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">The first referral you have invited or received from the System transfers 0.08 ETH to you. And from 3 referrals of the 1st line you can receive 0.24 ETH in total.</div>
                                <div className="font-bold text-lg">4.2 (2 level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income2.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">Then you transfer all 0.24 ETH to the upline of your upline (to your second-line upline), thereby purchasing the 2nd level.</div>
                                <div className="">The second and all the next levels can be bought immediately after the purchase of the previous level, without waiting for all the slots to be filled with referrals. More info below.</div>
                                <div className="">When you acquire level 2 from your second line upline, referrals of your referrals (or your referrals of the 2nd line ) can buy level 2 from you. 9 people transferred 0.24 ETH to you and you have 2.16 ETH on the second level as total.</div>
                                <div className="font-bold text-lg">4.3. (3 level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income3.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">After the purchase of the second level You can purchase 3th level, by making a transaction of 0.72 ETH to your 3rd line upline.</div>
                                <div className="">Now 27 referrals of the 3rd line can buy the 3rd level from you. In total, 19.44 ETH can be transferred to you already at the 3rd level.</div>
                                <div className="font-bold text-lg">4.4. (4 level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income4.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">You can purchase the 4th level by making a 2.16 ETH transfer to your 4th line upline.
81 referrals of the 4th line can make transfer to you of 2.16 ETH from every of them. And already at the 4th level you can be transferred for 174 ETH in total.</div>
                                <div className="font-bold text-lg">4.5. (5 level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income5.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">You can buy level 5 by making a transaction of 6.48 ETH (ATTENTION!) again to your 1st line upline (to your direct inviter to the CRYPTOFAIR System).</div>
                                <div className="">After that Your 1st line referrals can purchase the 5th level from You. Each of 3 referrals send You 6.48 ETH. In total, you can be transferred for 19.44 ETH on the 5th level.</div>
                                <div className="font-bold text-lg">4.6 (6 level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income6.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">Level 6 can be bought from your line 2 upline (from which YOU used to buy Level 2) by making a transfer of 19.44 ETH.</div>
                                <div className="">After purchasing the 6th level, your second line referrals can purchase a 6th level from you. 9 people can transfer to you 19.44 ETH by every of them. In total, you will be transferred for 174.96 ETH on the 6th level.</div>
                                <div className="font-bold text-lg">4.7. (7 level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income7.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">To buy the 7th level, you have to transfer 58.32 ETH to your 3rd line upline.</div>
                                <div className="">After purchasing the 7th level, referrals of the 3rd line can buy the 7th level from you . 27 people can transfer 58.32 ETH to you by every of them. In total on the 7th level you can be transferred for 1574.64 ETH.</div>
                                <div className="font-bold text-lg">4.8. (8 level)</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Income8.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">To purchase the last 8th level, you have to make a transfer of 174.96 ETH to your 4th line upline.</div>
                                <div className="">After purchase of 8th level You can be accessed for stransactions of 174.96 ETH from 81 Your referrals of 4th line. In total on the 8th level you can receive 14,171.76 ETH.</div>
                                <div className="font-bold text-lg">4.9 Total profit.</div>
                                <div className="">
                                    <div className="">
                                        <img src="/static/Totalprofit.png" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="font-bold text-lg text-red-400">4.10 Note.</div>
                                <div className="">
                                    <div className="">
                                        <img src="" alt="" className="mx-auto"/>
                                    </div>
                                </div>
                                <div className="">If someone wants to buy a level more than you currently have, the funds for the purchase of a level will be automatically redirected to your upline from which you have bought this level.</div>
                                <div className="">Example: For some reason, you have stopped participating in the system or have lost access to your ethereum wallet. Suppose you have a 3rd level and your 4th line referral buys a 4th level from you. The money is redirected to your 4th line upline.</div>
                                <div className="">You will have enough time to buy each level in advance.</div>
                                <div className="">BUT! If someone has dropped out or did not buy the 4th level in your structure, then you, in addition to the remaining 80 transfers, will receive an additional 81 transfers for the 4th level, which are redirected from your 4th line referral to you.</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="">
                    <div className="text-center text-2xl font-bold">Are you a leader?</div>
                    <div className=""><span className="font-bold">CRYPTOFAIR</span> provides <span className="">8 levels of repeated profit</span>, depending on your affiliate structure</div>
                </div>
            </div>
            <Footer />
        </div>
    )
}